@import "../../styles/variables.scss";

.restaurant-page {
  &__menu-item-title {
    font-size: 20px;
    line-height: 21px;
    margin: 40px 0 21px;
    text-transform: capitalize;
  }

  &__menu-container {
    --menu-width: 340px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(var(--menu-width), 1fr));
    gap: 40px 20px;

    @media (max-width: $break-point-phone) {
      --menu-width: 280px;
      gap: 20px;
    }
  }
}

@media (max-width: $break-point-tablet) {
  .restaurant-page__menu {
    padding: 0 10px;
  }
}